@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700);
.App{margin-left:calc(100vw - 100%);margin-right:0;font-family:"Open Sans",sans-serif;font-size:14px;-webkit-font-smoothing:antialiased !important}
.navbar-brand{font-weight:600;letter-spacing:1px;text-transform:uppercase}.navbar-nav .nav-item{letter-spacing:1px;margin-left:20px;text-align:center;text-transform:uppercase;cursor:pointer}.navbar-nav .nav-item:hover{color:#ffc952 !important}.navbar-nav a{color:#212529 !important;display:inline;font-size:1rem}.navbar-nav a:hover{color:#ffc952 !important}.navbar-nav svg{font-size:2em}
.table .row{justify-content:center}.table thead>tr:first-child{display:none}.table thead>tr th{background:#fff;position:-webkit-sticky;position:sticky;top:0}.table .nav-link{padding:0}.table #difficultyProgress{margin-bottom:25px;font-size:11.5px}.table #difficultyProgress>div:nth-child(n+2){margin-top:12px}.table .progress-pie{height:75px}.table .progress-bar-sm{height:5px}.table .easy{background-color:#5cb85c}.table .medium{background-color:#f0ad4e}.table .hard{background-color:#d9534f}.table .companies,.table .patterns{justify-content:space-evenly;margin:0}.table .companies img{padding:5px}.table .react-toggle{top:7px}.table .reset-button{margin-top:10px;margin-bottom:10px;font-size:.7rem}
.pattern-count{padding:.75em}.pattern-count h5{font-weight:bold}.pattern-count .badge{margin:.25em .25em}
.tips{text-align:center !important;display:block;overflow-x:auto;padding:.5em;color:#333;background:#f8f8f8}
.acknowledgements{justify-content:center}.acknowledgements .row{justify-content:center}.acknowledgements h1{text-align:center}.acknowledgements .nav-link{padding:0}
.tab-pane .active{display:flex !important;justify-content:center}.nav-link:hover{cursor:pointer}
